
import {
  computed,
  defineComponent,
  PropType,
  ref,
  toRefs,
  watch,
  watchEffect,
} from "vue";

export default defineComponent({
  props: {
    vertical: {
      type: Boolean,
      required: false,
      default: false,
    },
    horizontal: {
      type: Boolean,
      required: false,
      default: true,
    },
    scale: {
      type: Boolean,
      required: false,
      default: false,
    },
    rotate: {
      type: Boolean,
      required: false,
      default: false,
    },
    autoPlay: {
      type: Boolean,
      default: true,
      required: false,
    },
    autoPlaceInterval: {
      type: Number,
      default: 2000,
      required: false,
    },
    modelValue: {
      type: Array as PropType<{ value: number; url: string }[]>,
      default: 2000,
      required: false,
    },
    index: {
      type: Number,
      default: 0,
      required: false,
    },
  },
  setup(props, { emit }) {
    const activeIndex = ref(0);

    const {
      vertical,
      horizontal,
      autoPlay,
      autoPlaceInterval,
      modelValue,
      index,
    } = toRefs(props);

    const verticalClasses = (index: number) =>
      vertical.value && {
        "translate-y-full opacity-0": index !== activeIndex.value,
        "translate-y-0  opacity-100": index === activeIndex.value,
      };

    const horizontalClasses = (index: number) =>
      horizontal.value && {
        "translate-x-full opacity-100": index === activeIndex.value,
        "right-full top-0": true,
      };

    const itemChangedEvent = () => {
      emit(
        "update:index",
        modelValue.value[activeIndex.value].value || activeIndex
      );
    };

    const changeActiveIndex = (value: number) => {
      activeIndex.value = activeIndex.value + value;
      itemChangedEvent();
    };

    const leftDisabled = computed(() => {
      return activeIndex.value === 0;
    });

    const rightDisabled = computed(() => {
      return activeIndex.value === props.modelValue.length - 1;
    });

    watch(index, (newIndex) => {
      changeActiveIndex(newIndex - activeIndex.value);
    });

    let interval;
    let increment = true;

    watchEffect(() => {
      if (autoPlay.value) {
        interval = setInterval(() => {
          if (rightDisabled.value) increment = false;
          else if (leftDisabled.value) increment = true;
          if (increment) changeActiveIndex(+1);
          else changeActiveIndex(-1);
        }, autoPlaceInterval.value);
      } else {
        interval && clearInterval(interval);
      }
    });

    return {
      verticalClasses,
      horizontalClasses,
      changeActiveIndex,
      activeIndex,
      leftDisabled,
      rightDisabled,
    };
  },
});
